import Vue from "vue";
import Vuex from "vuex";
import http from "@/util/http-common";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    qnas: [], // 전체 게시글
    qna: {}, // update, search 할때 현재 보는 게시글
  },
  getters: {
    qnas(state) {
      return state.qnas;
    },
    qna(state) {
      return state.qna;
    },
  },
  mutations: {
    setQnas(state, payload) {
      state.qnas = payload;
    },
    setQna(state, payload) {
      state.qna = payload;
    },
  },
  actions: {
    getQnas(context) {
      http
        .get("qna")
        .then(({ data }) => {
          context.commit("setQnas", data);
        })
        .catch(() => {
          alert("수행 중 오류가 발생했습니다.");
        });
    },
    getQna(context, payload) {
      http
        .get(payload)
        .then(({ data }) => {
          context.commit("setQna", data);
        })
        .catch(() => {
          alert("오류 발생");
        });
    },
  },
});
