<template>
  <div>
    <h4 class="text-center">게시글 목록</h4>
    <div class="text-right">
      <button class="btn btn-primary" @click="movePage">등록</button>
    </div>
    <div v-if="qnas.length > 0">
      <table class="table table-qnaered table-condensed">
        <colgroup>
          <col width="10%" />
          <col width="50%" />
          <col width="15%" />
          <col width="25%" />
        </colgroup>
        <tr>
          <th>번호</th>
          <th>제목</th>
          <th>작성자</th>
          <th>날짜</th>
        </tr>
        <tr v-for="(qna, index) in qnas" :key="index">
          <td>{{ qna.no }}</td>
          <td>
            <router-link :to="'/qnaPage/search?no=' + qna.no">{{ qna.title }}</router-link>
          </td>
          <td v-text="qna.id"></td>
          <td>{{ qna.regtime | toDate }}</td>
        </tr>
      </table>
    </div>
    <div v-else>게시글이 없습니다.</div>
  </div>
</template>
<script>
// import http from '@/util/http-common.js'
import moment from "moment";
import {mapGetters} from "vuex";

// import axios from "axios";

export default {
  name: "list",
  computed:{
    ...mapGetters(["qnas"])
  },
  filters: {
    toDate: function (regtime) {
      return moment(new Date(regtime)).format("YYYY.MM.DD");
    }
  },
  methods: {
    movePage() {
      this.$router.push("/qnaPage/create");
    }
  },
  created() {
    this.$store.dispatch("getQnas");
  }
};
</script>
