<template>
  <div class="container">
    <h2 class="text-center">게시글 등록</h2>
    <table class="table">
      <tr>
        <th>글쓴이</th>
        <td>
          <input type="text" v-model="id" ref="id" id="id" placeholder="작성자를 입력해주세요" />
        </td>
      </tr>
      <tr>
        <th>제목</th>
        <td>
          <input type="text" v-model="title" ref="title" id="title" placeholder="제목을 입력해주세요" />
        </td>
      </tr>
      <tr>
        <th>질문</th>
        <td>
          <textarea id="question" v-model="question" ref="question" cols="30" rows="10"></textarea>
        </td>
      </tr>
      <tr>
        <th>답변</th>
        <td>
          <textarea id="answer" v-model="answer" ref="answer" cols="30" rows="10"></textarea>
        </td>
      </tr>
    </table>

    <div class="text-right">
      <button class="btn btn-primary" @click="createHandler">등록</button>
      <button class="btn btn-primary" @click="moveHandler">목록</button>
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      id: "",
      title: "",
      question: "",
      answer: ""
    };
  },
  methods: {
    createHandler() {
      //데이타 검증
      let err = true;
      let msg = "";

      !this.id && ((msg = "작성자를 입력해 주세요"), (err = false), this.$refs.id.focus());
      err &&
        !this.title &&
        ((msg = "제목을 입력해 주세요"), (err = false), this.$refs.title.focus());
      err &&
        !this.question &&
        ((msg = "질문을 입력해 주세요"), (err = false), this.$refs.question.focus());
      err &&
        !this.answer &&
        ((msg = "답변을 입력해 주세요"), (err = false), this.$refs.answer.focus());

      if (!err) {
        alert(msg);
      } else {
        axios
          .post("http://localhost:8080/qna", {
            id: this.id,
            title: this.title,
            question: this.question,
            answer: this.answer
          })
          .then(({ data }) => {
            if (data == "success") {
              alert("등록이 완료 됐습니다.");
              this.moveHandler();
            } else {
              alert("등록 실패");
            }
          })
          .catch(() => {
            alert("error 발생");
          });
      }
    },
    moveHandler() {
      this.$router.push("/qnaPage");
    }
  }
}
</script>
