<template>
  <div class="container">
    <h4 class="text-center">Q&A 수정</h4>
    <table class="table">
      <tr>
        <th>제목</th>
        <td>
          <input type="text" id="title" ref="title" placeholder="제목을 입력해주세요" v-model="qna.title" />
        </td>
      </tr>
      <tr>
        <th>질문</th>
        <td>
          <textarea id="question" cols="30" rows="10" ref="question" v-model="qna.question"></textarea>
        </td>
      </tr>
      
      <tr>
        <th>답변</th>
        <td>
          <textarea id="answer" cols="30" rows="10" ref="answer" v-model="qna.answer"></textarea>
        </td>
      </tr>
    </table>

    <div class="text-right">
      <button class="btn btn-primary" @click="updateHandler">수정</button>
      <button class="btn btn-primary" @click="moveHandler">목록</button>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import {mapGetters} from "vuex";

export default {
  computed:{
    ...mapGetters(['qna'])
  },
  methods: {
    updateHandler: function() {
      let err = true;
      let msg = "";
      //필수 요건 검사
      err &&
        !this.qna.title &&
        ((msg = "제목을 입력해 주세요"), (err = false), this.$refs.title.focus());
      err &&
        !this.qna.question &&
        ((msg = "내용을 입력해 주세요"), (err = false), this.$refs.question.focus());

      if (!err) alert(msg);
      else {
        axios
          .put(`http://localhost:8080/qna`, {
            no: this.qna.no,
            title: this.qna.title,
            id: this.qna.id,
            question: this.qna.question,
            answer: this.qna.answer
          })
          .then(({ data }) => {
            if (data == "success") {
              console.log("update................", data);
              alert("수정 완료!!!");
              this.moveHandler();
            } else {
              alert("수정 중 오류 발생");
            }
          })
          .catch(() => {
            alert("오류 발생");
          });
      } // else end
    }, //function end
    moveHandler: function() {
      this.$router.push("/qnaPage");
    }
  }
}
</script>   
