<template>
  <div class="container center-block">
    <h4 class="text-center">{{qna.no}}번 Q&A</h4>
    <table class="table" v-if="qna">
      <tr class="text-center">
        <th>번호</th>
        <td v-text="qna.no"></td>
        <th>글쓴이</th>
        <td v-text="qna.id"></td>
      </tr>
      <tr class="text-center">
        <th>제목</th>
        <td v-text="qna.title"></td>
        <th>날짜</th>
        <td>{{ qna.regtime | toDate }}</td>
      </tr>
      <tr>
        <th class="text-center" colspan="4">질문</th>
      </tr>
      <tr>
        <td class="text-center" colspan="4" v-text="qna.question"></td>
      </tr>
      <tr>
        <th class="text-center" colspan="4">답변</th>
      </tr>
      <tr>
        <td class="text-center" colspan="4" v-text="qna.answer"></td>
      </tr>
    </table>

    <div class="text-right">
      <router-link class="btn btn-primary" to="/qnaPage">목록</router-link>
      <router-link class="btn btn-primary" :to="'/qnaPage/update?no=' + qna.no">수정</router-link>
      <router-link class="btn btn-primary" :to="'/qnaPage/remove?no=' + qna.no">삭제</router-link>
    </div>
  </div>
</template>
<script>
// import axios from "axios";
import moment from "moment";
import {mapGetters} from "vuex";

export default {
  computed:{
    ...mapGetters(["qna"])
  },
  filters: {
    toDate: function (regtime) {
      return moment(new Date(regtime)).format("YYYY.MM.DD");
    }
  },
  created() {
    console.log('search.........', this.$route.query.no);
    this.$store.dispatch("getQna", `/qna/${this.$route.query.no}`)
  }
};
</script>
